<template>
  <div class="notes row justify-content-center d-flex">
    <div class="col-12">
      <div class="card">
        <div class="card-body">
          <h1>{{note.creator}}</h1>
          <h4>{{note.content}}</h4>
          <h4>{{note.flagged}}</h4>
          <div>
            <button @click="removeNote()" class="btn btn-danger"><i class="fas fa-dumpster-fire"></i></button>
          </div>
        </div>
        <div class="card-footer justify-content-end d-flex">
          <div class="dropdown">
            <button class="btn btn-info dropdown-toggle" type="button" id="dropdownMenuButton"
              data-toggle="dropdown">Change Status</button>
            <div class="dropdown-menu">
              <p class="dropdown-item bg-warning" @click="editNote('pending')">Pending</p>
              <p class="dropdown-item bg-success" @click="editNote('completed')">Completed</p>
              <p class="dropdown-item bg-danger" @click="editNote('rejected')">Rejected</p>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'notes',
    data() {
      return {
      }
    },
    props: ['note'],
    computed: {
      notes() {
        return this.$store.state.notes
      }
    },
    methods: {
      editNote(status) {
        this.note.flagged = status
        this.$store.dispatch('editNote', this.note)
      },
      removeNote() {
        this.$store.dispatch('removeNote', this.note)
      }
    }
  }

</script>

