import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import moment from 'moment'
import router from './router'

Vue.use(Vuex)

let _bugApi = axios.create({
  baseURL: 'https://bcw-sandbox.herokuapp.com/api/chrissyz'
})

export default new Vuex.Store({
  state: {
    bugs: [],
    activeBug: {},
    notes: []
  },
  mutations: {
    setBugs(state, data) {
      state.bugs = data
    },
    setActiveBug(state, data) {
      state.activeBug = data
    },
    setNotes(state, data) {
      state.notes = data
    }
  },
  actions: {
    //BUGS
    getAllBugs({ commit, dispatch }) {
      _bugApi.get('bugs')
        .then(res => {
          console.log(res.data)
          let results = res.data.results
          commit('setBugs', results)
        })
    },
    addBug({ commit, dispatch }, bugData) {
      _bugApi.post('bugs', bugData)
        .then(res => {
          dispatch('getAllBugs')
        })
    },
    editBug({ commit, dispatch }, payload) {
      _bugApi.put('bugs/' + payload._id, payload)
        .then(res => {
          console.log("updated bug", res)
          dispatch('getBug', res.data._id)
        })
    },
    deleteBug({ commit, dispatch }, bugId) {
      _bugApi.delete('bugs/' + bugId)
        .then(res => {
          dispatch('getAllBugs')
          router.push({ name: 'home' })
        })
    },
    getBug({ commit, dispatch }, bugId) {
      _bugApi.get('bugs/' + bugId)
        .then(res => {
          commit('setActiveBug', res.data.results)
          dispatch('getNotes', res.data.results._id)
        })
    },
    //NOTES
    getNotes({ commit, dispatch }, bugId) {
      _bugApi.get('bugs/' + bugId + '/notes')
        .then(res => {
          console.log('notes for this bug', res.data)
          commit('setNotes', res.data.results)
        })
    },
    addNote({ commit, dispatch }, payload) {
      _bugApi.post('bugs/' + payload.bug + '/notes', payload)
        .then(res => {
          console.log('successfully added note', res)
          debugger
          dispatch('getNotes', payload.bug)
        })
    },
    editNote({ commit, dispatch }, payload) {
      _bugApi.put('bugs/' + payload.bug + '/notes/' + payload._id, payload)
        .then(res => {
          console.log('edited note', res)
          dispatch('getNotes', payload.bug)
        })
    },
    removeNote({ commit, dispatch }, payload) {
      _bugApi.delete('bugs/' + payload.bug + '/notes/' + payload._id, payload._id)
        .then(res => {
          console.log('deleted note', res)
          dispatch('getNotes', payload.bug)
        })
    }
  },
  getters: {
    FormatTime: () => (time) => {
      return moment(String(time)).format('MM/DD/YYYY hh:mm')
    },
    Completed: (state) => (status) => {
      if (status) {
        return state.bugs.filter(b => b.closed == status)
      }
      return state.bugs
    }
  }
})
