<template>
  <div class="bugsTable row mt-2 justify-content-center d-flex">
    <div class="col-12">
      <button @click="filterCompleted(filter)" class="btn my-2"
        :class="{'btn-danger': filter, 'btn-primary': !filter}">{{filter ? 'Show All' : 'Filter By Completed'}}</button>
    </div>

    <table class="table table-bordered col-10">
      <thead>
        <tr class="table-light">
          <th scope="col">Date Created</th>
          <th scope="col">Title</th>
          <th scope="col">Reported By</th>
          <th scope="col">Status</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="bug in bugs" class="table-success" :class="[bug.closed ? 'table-warning' : '']"
          @click="$router.push({name: 'bugDetails', params: {id: bug._id}})">
          <td>{{formatTime(bug.createdAt)}}</td>
          <td>{{bug.title}}</td>
          <td class="justify-content-start d-flex"><i class="fas fa-user-circle"></i> {{bug.creator}}</td>
          <td>{{formatClosed(bug.closed)}}</td>
        </tr>
      </tbody>
    </table>

  </div>
</template>

<script>
  import moment from 'moment'

  export default {
    name: 'bugsTable',
    data() {
      return {
        filter: false
      }
    },
    mounted() {
    },
    computed: {
      bugs() {
        return this.$store.getters.Completed(this.filter)
        // return this.$store.state.bugs
      }
    },
    methods: {
      formatTime(time) {
        return this.$store.getters.FormatTime(time)
      },
      formatClosed(status) {
        if (!status) {
          return "Open"
        }
        return "Closed"
      },
      filterCompleted(filter) {
        this.filter = !filter

      }

    }
  }


</script>

